.hero-section {
    position: relative;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #FFFFFF;
    text-align: center;
    transition: background-image 1s ease-in-out;
}

.hero-section::before {
    content: ""; 
    position: absolute; 
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.65); 
    z-index: 0;
}

.hero-content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style: none;
}

.hero-content img {
    max-height: 200px;
    margin-bottom: 25px;
    filter: brightness(0) invert(50%) sepia(100%) saturate(5000%) hue-rotate(320deg) brightness(95%) contrast(105%);
}

.scroll-to-section-button {
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    margin: 0;
}

.scroll-to-section-button img {
    display: block;
    width: 200px;
}

.scroll-to-section-button img:hover {
    filter: brightness(100%) invert(0%) sepia(0%) saturate(100%) hue-rotate(0deg) contrast(100%);
}
