.portfolio_wrapper {
    margin-top: 20px;
}

.portfolio_group {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    list-style: none;

}

.portfolio_item {
    width: calc(33.333% - 20px); 
    height: 350px;
    position: relative;
}

.image-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.image-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transition: opacity 0.3s ease-in-out;
}

.hover-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(233, 30, 99, 0.8);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    text-align: center;
    padding: 1rem;
}

.image-wrapper:hover .hover-overlay {
    opacity: 1;
}

.column_filters {
    margin-bottom: 20px;
}

.isotope-filters .filters_wrapper {
    margin: 0;
}

.isotope-filters .categories {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: center;
}

.isotope-filters .categories li {
    display: inline-block;
    margin: 0 15px;
}

.isotope-filters .categories li.current-cat a {
    font-weight: bold;
}

.isotope-filters .categories li {
    list-style: none;
}

#portfolio {
    padding: 20px 0;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    overflow: hidden;
}

.modal-content {
    position: relative;
    width: 80%;
    height: 100%;
    max-width: 90%;
    max-height: 90%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    padding: 20px;
}

.modal-image {
    width: 50%;
    height: auto;
    max-height: 100%;
    max-width: 100%;

    object-fit: cover;
}

.modal-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: #333;
    color: #fff;
    border: none;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 2rem;

    z-index: 1001;
}

.modal-close:hover {
    background: #555;
}

.modal-description {
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    padding: 10px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    text-align: center;
}
  