body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background: #000;
  color: #fff;
  list-style: none;
  overflow-x: hidden;
}

a {
  color: inherit;
  text-decoration: none !important;
}

a:hover {
  color: #ffc9db;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.list-style-none {
  list-style: none;
}

@media (max-width: 768px) {
  .header {
    padding: 1rem;
  }

  .nav ul {
    flex-direction: column;
    gap: 1rem;
  }

  .menu-button {
    font-size: 1.5rem;
  }
}
