body {
    margin: 0;
    padding: 0;
  }



.content-section {
    min-height: 100vh;
    box-sizing: border-box;
    text-align: center; 
    font-family: 'Inter', sans-serif;
    padding: 0;
    list-style: none;
  }

.nav-bar {
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    background-color: #2A2A2A;
    border-bottom: 1px solid #B0B0B0;
    padding: 1rem;
    z-index: 100;
    justify-content: space-between;
}

.nav-logo {
    flex-shrink: 0;
}

.nav-logo img {
    height: 40px;
    filter: brightness(0) invert(50%) sepia(100%) saturate(5000%) hue-rotate(320deg) brightness(95%) contrast(105%);

}

nav {
    flex-grow: 1;
}

nav ul {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0;
    margin: 0;
}

nav ul li {
    margin: 0 1rem;
}

nav ul li a {
    color: #E91E63;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 500;
    transition: color 0.3s;
}

nav ul li a:hover {
    color: #F48FB1;
}

.section {
    padding: 4rem 2rem;
    margin-bottom: 2rem;
    border-bottom: 1px solid #2A2A2A;
    text-align: center;
    list-style: none;
}

.section h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #E91E63; 
}

.section p {
    font-size: 1rem;
}

.contact-form {
    max-width: 600px;
    margin: 0 auto;
    padding: 2rem;
    background-color: #2A2A2A; 
    border-radius: 8px;
  }
  
  .contact-form div {
    margin-bottom: 1rem;
  }
  
  .contact-form label {
    display: block;
    margin-bottom: 0.5rem;
    color: #E91E63;
  }
  
  .contact-form input,
  .contact-form textarea {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #B0B0B0;
    border-radius: 4px;
    background-color: #1E1E1E; 
    color: #FFFFFF;
  }
  
  .contact-form button {
    background-color: #E91E63; 
    color: #FFFFFF;
    border: none;
    padding: 1rem;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    width: 100%;
  }
  
  .contact-form button:hover {
    background-color: #F48FB1; 
  }
  